import React, { useState } from "react";
import '../LoginPage.css'
import Change from '../pictures/change.png'
import { Button, Divider, Drawer, Input, Select, ConfigProvider, Upload, Checkbox, notification } from 'antd';
import { CloseOutlined, CaretDownOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
import PdfIcon from '../pictures/pdf.svg'
import VideoIcon from '../pictures/video.svg'
import ImageIcon from '../pictures/image.svg'
import _ from "lodash";
import { formatFileSize } from "../utills/computation";

const options = [
    {
        label: '+86 ',
        value: '+86',
    },
    {
        label: '+1 ',
        value: '+1',
    },
    {
        label: '+2 ',
        value: '+2',
    },
    {
        label: '+852 ',
        value: '+852',
    },
];

function LoginPage() {
    const [phoneError, setPhoneError] = useState(false)
    const verification = () => {
        const phoneNumber = /^1[3456789]\d{9}$/;
        if (!phoneNumber.test(mobile)) {
            setPhoneError(<><ExclamationCircleOutlined /> 11</>);
        } else {
            setPhoneError(null);
        }
    };
    const [isFocus, setIsFocus] = useState(false)
    const [mobile, setMobile] = useState()
    //
    const [open, setOpen] = useState(false);
    //
    const showDrawer = () => {
        setOpen(true);
    };
    //
    const onClose = () => {
        setOpen(false);
    };
    //
    // const toggleDrawer = () => {
    //     setOpen(!open);
    // };
    //
    const [selectedFiles, setSelectedFiles] = useState([]);
    // 
    const [showFileBox, setShowFileBox] = useState(false);
    // 
    const [showInfo, setShowInfo] = useState(true);

    let notificationShow = [false]
    //
    const handleUploadChange = (info) => {
        //
        let savedFiles = [...selectedFiles];
        //fileListUpload
        let newFiles = [...info.fileList];
        // 100MB
        const OverLimit = newFiles.some(newFile => newFile.size > 100 * 1024 * 1024);

        if (OverLimit && notificationShow) {
            // 
            notification.open({
                description: ``,
                placement: 'bottomLeft',
                style: {
                    width: 370,
                    fontWeight: "bold",
                    backgroundColor: 'rgba(240, 197, 79, 1)',
                },
            });
            notificationShow = true; 
            return;
        } else {
            // 
            newFiles.forEach(newFile => {
                //
                const isDuplicate = _.some(savedFiles, { 'name': newFile.name });
                if (!isDuplicate) {
                    savedFiles = _.concat(savedFiles, newFile);
                }
            });
        }
        // 
        if (savedFiles.length > 10) {
            savedFiles = savedFiles.slice(-10)
        }
        setSelectedFiles(savedFiles);
        setShowFileBox(true);
        setShowInfo(false); //  rectangle

    }
    //
    const Pdf = () => {
        return (
            <div>
                <img src={PdfIcon} alt='Pdf' />
            </div>
        )
    }
    const Image = () => {
        return (
            <div>
                <img src={ImageIcon} alt="" />
            </div>
        )
    }
    const Video = () => {
        return (
            <div>
                <img src={VideoIcon} alt='Video' />
            </div>
        )
    }
    const getTotalFileSize = () => {
        let totalSize = 0;
        selectedFiles.forEach(file => {
            totalSize += file.size;
        });
        //  formatFileSize 
        const totalSizeStr = formatFileSize(totalSize);
        return totalSizeStr;
    };

    const [isHovered, setIsHovered] = useState(null);

    const handleMouseEnter = (index) => {
        setIsHovered(index);
    };
    const handleMouseLeave = () => {
        setIsHovered(null);
    };
    const handleDeleteFile = (index) => {
        const updatedFiles = [...selectedFiles];
        updatedFiles.splice(index, 1);
        setSelectedFiles(updatedFiles);
        // rectangle
        if (updatedFiles.length === 0) {
            setShowFileBox(false);
            setShowInfo(true);
            // let closeInfo = document.querySelector(".file-box")
            // console.log(closeInfo)
            // closeInfo.style.animationName = "return";
            // closeInfo.style.animationDuration = '0.5s';
            // closeInfo.style.animationFillModel = "forwards";
        }
    };

    //
    const [api, contextHolder] = notification.useNotification();
    const openNotification = (placement) => {
        //api
        api.open({
            description:
                '',
            placement,
            style: {
                width: 370,
                fontWeight: "bold",
                backgroundColor: 'rgba(240, 197, 79, 1)',
            },
            maxCount:1
        });
    };
    //
    const [isChecked, setIsChecked] = useState(true);
    return (
        <>
            <div className="picture-background">
                <span className="meetingpd">
                    MEETINGPAD
                </span>
                {/*  */}
                {/* <div onClick={toggleDrawer}> */}
                <Upload
                    //
                    multiple={true}
                    fileList={[]}
                    accept=".pdf,.jpg,.jpeg,.png,.gif,.mp4,.mov"
                    onChange={handleUploadChange}
                >
                    {showInfo && ( //  rectangle
                        <span className="rectangle" >
                            <span className="plus">+</span>
                            <span className="add"></span>
                        </span>
                    )}
                </Upload>
                {showInfo && (
                    <span className="balck-square " >
                        <span className="text-square">
                             PDF 100 MB10
                        </span>
                    </span>
                    
                )}
                {/*  */}
                {showFileBox && (
                    <div className="file-box" >
                        <div>
                        </div>
                        <span className="text-transfer"></span>
                        <span className="total-size"> {getTotalFileSize()} </span>
                        <Upload
                            //
                            multiple={true}
                            //
                            fileList={[]}
                            accept=".pdf,.jpg,.jpeg,.png,.gif,.mp4,.mov"
                            onChange={handleUploadChange}
                        >
                            <span className="text-round">
                                <span className="text-add">+</span>
                            </span>
                        </Upload>
                        <div style={{ overflow: "auto", height: 140 }}>
                            {/*  */}
                            {_.map(selectedFiles, (file, index) => {
                                const fileSize = formatFileSize(file.size);
                                // 
                                const fileExtension = file.name.split('.').pop().toLowerCase();
                                let fileIcon;
                                //  SVG 
                                switch (fileExtension) {
                                    case 'pdf':
                                        fileIcon = <Pdf />;
                                        break;
                                    case 'jpg':
                                    case 'jpeg':
                                    case 'png':
                                    case 'gif':
                                        fileIcon = <Image />;
                                        break;
                                    case 'mp4':
                                    case 'mov':
                                        fileIcon = <Video />;
                                        break;
                                    default:
                                        fileIcon = null;
                                }
                                return (
                                    <>
                                        <div key={index} className="file-item" onMouseEnter={() => handleMouseEnter(index)} onMouseLeave={() => handleMouseLeave(index)}>
                                            {fileIcon}
                                            <div style={{ marginLeft: 10 }}>
                                                <div className="file-name">{file.name}</div>
                                                <div className="file-size"> {fileSize}</div>
                                            </div>
                                            {isHovered === index && (
                                                <div className="close-x" onClick={() => handleDeleteFile(index)}>x</div>
                                            )}
                                        </div>
                                    </>
                                );
                            })}
                        </div>
                        <div className="text-container">
                            <div>
                                <span className="text-left"></span><span className="text-right" style={{ marginLeft: 194 }}>7</span><br></br>
                            </div>
                            <div style={{ marginTop: 15 }}>
                                <span className="text-left"></span><span className="text-right" style={{ marginLeft: 40 }}>PDF</span><br></br>
                            </div>
                            <div style={{ marginTop: 15 }}>
                                <span className="text-left"></span><span className="text-right" style={{ marginLeft: 108 }}>100MB</span><br></br>
                            </div>
                            <div style={{ marginTop: 15 }}>
                                <span className="text-left"></span><span className="text-right" style={{ marginLeft: 122 }}>10</span><br></br>
                            </div>
                        </div>

                        <div className="text-bottom2">
                                
                        </div>

                        <Checkbox className="checkbox"
                            checked={isChecked}
                            //
                            onChange={(e) => setIsChecked(e.target.checked)}
                            //
                            defaultChecked={true} />
                        {contextHolder}
                        <Button className="text-button" type="primary"
                            // onClick={() => openNotification('bottomLeft')}
                            onClick={() => {
                                if (!isChecked) {
                                    openNotification('bottomLeft');
                                } else {
                                    //
                                }
                            }}
                        ></Button>

                    </div>
                )}
                <span className="rectangle-right" style={{ zIndex: 99999 }}>
                    <img src={Change} alt={''} className="language-change " />
                    {/*  */}
                    <div className="background" onClick={onClose}>
                    </div>
                    <Divider type="vertical" style={{ marginRight: 110 }} />
                    <div>
                        <span className="console" >
                            
                        </span>
                        <span className="registration" onClick={showDrawer}>/</span>
                    </div>
                    {/*  */}
                    {open && <CloseOutlined style={{ marginLeft: 75 }} onClick={onClose} />}
                </span>
                <Drawer
                    mask={false}
                    onClose={onClose}
                    open={open}
                    width='600px'
                    // 
                    closeIcon={false}
                >
                    <div className="drawer">
                        <div className="text1-drawer text">/</div>
                        <div className="text2-drawer">/</div>
                        <div>
                            {/*  */}
                            <span className={`text-number ${isFocus ? 'text-focus' : 'text-none'} ${phoneError ? 'text-error' : ''}`}></span>
                            <ConfigProvider theme={{ token: { colorPrimary: '#F0C54F' } }} >
                                <Input
                                    className={`Input ${phoneError ? 'input-error' : ''}  ${mobile ? 'input-blur' : ''}`}
                                    onFocus={() => { setIsFocus(true); setPhoneError(null); }}
                                    //
                                    onChange={(e) => setMobile(e.target.value)}
                                    //
                                    onBlur={() => !!mobile || setIsFocus(false)}
                                    prefix={
                                        <Select
                                            variant="borderless"
                                            options={options}
                                            width="60"
                                            defaultValue='+86'
                                            optionLabelProp="value"
                                            popupMatchSelectWidth={false}
                                            defaultActiveFirstOption='true'
                                            suffixIcon={<CaretDownOutlined />}
                                            onClick={(e) => {
                                                e.preventDefault();
                                                e.stopPropagation();
                                            }}
                                        />
                                    } >
                                </Input>
                            </ConfigProvider>
                        </div>
                        {phoneError && <div className="error-text" >{phoneError}</div>}
                        <Button className="Button" onClick={verification}></Button>
                        <div className="text3-drawer">   </div>
                    </div>
                </Drawer>
                <span className="text-bottom">
                         ICP2022103850-1  35020602002560V1.3
                </span>
            </div>
        </>
    );
}
export default LoginPage;
